
.saman {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: rgb(247, 247, 247);
    z-index: 1;
  }

  
  .saman > img {
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
  }
  
  .saman__rating {
    display: flex;
  }
 .saman > button {
    background: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
  }
  
  .saman__Price {
    margin-top: 5px;
  }
  
  .saman__info {
    height: 100px;
    margin-bottom: 15px;
    justify-content: center;
  }
   
  